// @flow

import { type JsonSearchParams } from '../../types/utils'

export const search = (searchParams: JsonSearchParams, jsonObj: any): Array<any> => {
  // If jsonObj is undefined we can't compare anything and so we fail early
  if (typeof jsonObj === 'undefined') return []

  const {
    keyToMatch,
    valueToMatch,
    // keyToReturn,
    parent,
    tokenizeKeys = keyToMatch ? keyToMatch.split('.') : []
  } = searchParams

  // If we ran out of tokenizeKeys it means we need to verify that we found the right key with the correct value
  if (!tokenizeKeys.length) {
    if (typeof valueToMatch === 'undefined' || valueToMatch === jsonObj) {
      return parent ? [parent] : [jsonObj]
    }
  }

  const deepSearch = (parentObj: any, shiftToken?: boolean): Array<any> => {
    let results = []

    const searchParams = {
      keyToMatch,
      valueToMatch,
      // keyToReturn,
      parent: parentObj,
      tokenizeKeys
    }

    if (shiftToken) {
      const token = tokenizeKeys[0]
      searchParams.tokenizeKeys = tokenizeKeys.slice(1)
      return search(searchParams, parentObj[token])
    }

    if (!parent) searchParams.parent = undefined

    for (const key in parentObj) {
      const result = search(searchParams, parentObj[key])
      results = results.concat(result)
    }

    return results
  }

  // If we are searching in an Array, we would go over each child without moving the token counter
  // if (Array.isArray(jsonObj)) return deepSearch(searchParams, jsonObj)
  if (Array.isArray(jsonObj)) return deepSearch(jsonObj)

  // Shift one level based on the first key in tokenizeKeys with jsonObj as the parent
  // const result1 = deepSearch(searchParams, jsonObj, true)
  const result1 = deepSearch(jsonObj, true)
  // If there is no parent yet, it means that we still have the full tokenizeKeys
  // So we will try to use the same tokenizeKeys for every child of jsonObj
  // const result2 = !parent ? deepSearch(searchParams, jsonObj) : []
  const result2 = !parent ? deepSearch(jsonObj) : []
  return [ ...result1, ...result2 ]
}

